<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="flex flex-wrap-reverse items-center mb-base" v-if="viewOnly === false">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>

      <div id="instance-loading-container" class="nv-row row hover vs-con-loading__container">
        <template>
          <div class="nv-col w-full">
            <p class="info-row">
              <span>{{ $t('ID') }}:</span>
              <span>{{ workflowInstance.instanceId | placeholder('-') }}</span>
            </p>
            <p class="info-row" v-if="workflowInstance.externalBookId">
              <span>{{ $t('EXTERNAL_ID') }}:</span>
              <span>{{ workflowInstance.externalBookId }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('ACTION_TYPE') }}:</span>
              <span>{{ workflowInstance.actionTypeDescription | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('ACCOUNT_NUMBER') }}:</span>
              <span>{{ workflowInstance.accountNumber | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('DELIVERY_BRANCH') }}: </span>
              <span>{{ workflowInstance.deliveryBranchName | placeholder('-') }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('BOOK_COUNT') }}: </span>
              <span>{{ workflowInstance.bookCount }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('INPUT_BY') }}: </span>
              <span>{{ workflowInstance.inputUserFullName }}</span>
            </p>
            <p class="info-row">
              <span>{{ $t('CREATION_DATE') }}: </span>
              <span>{{ workflowInstance.creationDate | formatUnixDate }}</span>
            </p>
          </div>
        </template>
      </div>

      <vs-tabs class="mt-base">
        <vs-tab :label="$t('TASKS')">
          <div id="tasks-loading-container" class="vs-con-loading__container">
            <vs-table class="mb-base" :data="taskTableData" :noDataText="taskTableNoDataText" search>
              <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
                <div class="flex flex-wrap-reverse items-center">
                  <vs-button class="p-3 mr-4" @click="refreshTaskTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="taskTableBusy">{{ $t('REFRESH') }}</vs-button>
                </div>

                <div class="flex justify-end mr-4">
                  <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
                    <span class="mx-2">{{ taskTablePagingInfo }}</span>
                    <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                      <span class="far fa-angle-down text-primary"></span>
                      <vs-dropdown-menu>
                        <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="taskTablePerPage = item">
                          <span>{{ item }}</span>
                        </vs-dropdown-item>
                      </vs-dropdown-menu>
                    </vs-dropdown>
                  </div>
                </div>
              </div>

              <template slot="thead">
                <vs-th sort-key="taskId">
                  {{ this.$t('ID') }}
                </vs-th>
                <vs-th sort-key="assignedUserFullName">
                  {{ this.$t('ASSIGNED_USER') }}
                </vs-th>
                <vs-th sort-key="statusDescription">
                  {{ this.$t('STATUS') }}
                </vs-th>
                <vs-th sort-key="creationDate">
                  {{ this.$t('CREATION_DATE') }}
                </vs-th>
                <vs-th sort-key="dueDate">
                  {{ this.$t('DUE_DATE') }}
                </vs-th>
              </template>

              <template slot-scope="{data}">
                <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
                  <vs-td :data="data[index].taskId">
                    {{ data[index].taskId }}
                  </vs-td>
                  <vs-td :data="data[index].assignedUserFullName">
                    {{ data[index].assignedUserFullName }}
                  </vs-td>
                  <vs-td :data="data[index].statusDescription">
                    {{ data[index].statusDescription }}
                  </vs-td>
                  <vs-td :data="data[index].creationDate">
                    {{ data[index].creationDate | formatUnixDate }}
                  </vs-td>
                  <vs-td :data="data[index].dueDate">
                    {{ data[index].dueDate | formatUnixDate }}
                  </vs-td>
                </vs-tr>
              </template>
            </vs-table>

            <vs-pagination :total="taskTableTotalPages" v-model="taskTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
          </div>
        </vs-tab>
      </vs-tabs>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ChequeService from '@/services/cheque/ChequeService'
import WorkflowTaskTableMixin from '@/mixins/commons/WorkflowTaskTableMixin'

export default {
  name: 'cheque-book-workflow-instance-view',
  mixins: [AlertMixin, LoadingMixin, WorkflowTaskTableMixin],
  props: {
    transferId: {
      type: String,
      default: null
    },
    viewOnly: {
      type: Boolean,
      default: false
    }
  },
  data: function () {
    return {
      workflowInstance: {}
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('instance-loading-container')
      ChequeService.chequeBookWorkflowInstances({
        instanceId: this.instanceId
      }).then(function (response) {
        self.stopLoading('instance-loading-container')
        self.workflowInstance = response.data.instanceList[0] || {}
        self.loadTaskTableData('tasks-loading-container')
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route: function (to, from) {
      this.load()
    }
  }
}
</script>
