import WorkflowService from '@/services/commons/WorkflowService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let WorkflowTaskTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTaskTableFilter: {
      type: String,
      default: null
    },
    defaultTaskTableOrderBy: {
      type: String,
      default: null
    },
    instanceId: {
      type: String,
      default: null
    },
    workflowTypeEnumId: {
      type: String,
      default: null
    },
    taskTypeEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      instanceStatusId: null,
      taskTableData: [],
      taskTableFilterTimer: null,
      taskTableFilter: this.defaultTaskTableFilter,
      taskTableOrderBy: this.defaultTaskTableOrderBy,
      taskTableContainer: 'loading-container',
      taskTableBusy: false,
      taskTableTotalRows: 0,
      taskTableCurrentPage: 1,
      taskTablePerPage: 10
    }
  },
  computed: {
    taskTableNoDataText: function () {
      return this.taskTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    taskTableTotalPages: function () {
      return Math.ceil(this.taskTableTotalRows / this.taskTablePerPage)
    },
    taskTablePagingInfo: function () {
      return this.pagingInfo(this.taskTableCurrentPage, this.taskTablePerPage, this.taskTableTotalRows)
    }
  },
  methods: {
    loadTaskTableData (loadingContainerId) {
      let self = this
      this.taskTableBusy = true
      this.taskTableContainer = loadingContainerId || this.taskTableContainer
      this.startLoading(this.taskTableContainer)
      WorkflowService.workflowTasks({
        pageIndex: this.taskTableCurrentPage - 1,
        pageSize: this.taskTablePerPage,
        filter: this.taskTableFilter,
        orderByField: this.taskTableOrderBy,
        workflowTypeEnumId: this.workflowTypeEnumId,
        instanceId: this.instanceId,
        instanceStatusId: this.instanceStatusId,
        taskTypeEnumId: this.taskTypeEnumId,
        assignedUserId: self.$store.state.userId
      }).then(function (response) {
        self.stopLoading(self.taskTableContainer)
        self.taskTableBusy = false
        self.taskTableTotalRows = response.data.totalRows
        self.taskTableData = response.data.taskList || []
      }).catch(function (error) {
        self.stopLoading(self.taskTableContainer)
        self.taskTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTaskTable () {
      this.loadTaskTableData()
    },
    filterTaskTable (filter) {
      let self = this
      if (this.taskTableFilter !== filter) {
        clearTimeout(this.taskTableFilterTimer)
        this.taskTableFilterTimer = setTimeout(() => {
          self.taskTableFilter = filter
          self.loadTaskTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.taskTableFilterTimer)
      }
    },
    orderTaskTable (orderBy, active) {
      this.taskTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTaskTableData()
    }
  },
  watch: {
    taskTableCurrentPage: function () {
      this.refreshTaskTable()
    },
    taskTablePerPage: function () {
      this.refreshTaskTable()
    },
    taskTypeEnumId: function () {
      this.refreshTaskTable()
    }
  }
}

export default WorkflowTaskTableMixin
